<template>
  <div>
    <van-nav-bar title="支付结果" left-arrow @click-left="onClickleft"/>
    <van-row style="text-align: center;margin-top: 120px;">
      <img src="http://zhongjun.ueoa.net./wap/static/image/win.png" width="70px"/>
      <van-row style="margin: 10px 0px;">支付成功</van-row>
      <van-row style="margin-bottom: 30px;color: #FF0000">2000.00</van-row>

      <van-button plain hairline color="#999999" style="height: 35px; width: 100px;" @click="orderinfo">查看详情
      </van-button>
    </van-row>
  </div>
</template>

<script>
export default {
  name: "result",
  data() {
    return {}
  },
  methods: {
    //返回上一页
    onClickleft() {
      this.$router.go(-1);
    },
    orderinfo() {
      this.$router.push('order_info')
    }

  }
}
</script>

<style scoped>

</style>



